@import "../../../protons/global";

// Disables non-native backdrop
.jcc-full-modal[data-a11y-dialog-native] .jcc-full-modal__backdrop {
  display: none;
}

// Shows the dialog window. This attr is added by the A11yDialog script.
.jcc-full-modal__dialog[open] {
  display: block;
}

// Hides the dialog window. This attr is added by the A11yDialog script.
.jcc-full-modal [aria-hidden="true"] {
  display: none;
}

.jcc-full-modal__backdrop,
.jcc-full-modal__dialog::backdrop {
  // Structural Attributes
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  // Visual Attributes
  background-color: rgba(black, 0.2);
}

.jcc-full-modal__dialog {
  // Structural Attributes
  min-height: 80%;
  margin: 5% auto;
  width: 90%;
  max-width: 70rem;
  position: relative;
  @include u-padding(2);
  box-sizing: border-box;

  @include at-media(desktop) {
    @include u-padding(6);
  }

  // Visual Attributes
  border: 0;
  box-shadow: 8px 8px 32px rgba(black, 0.2);
}

.jcc-full-modal__close-button {
  position: absolute;
  webkit-appearance: none;
  top: 0;
  right: 0;

  @include at-media(desktop) {
    top: 32px;
    right: 32px;
  }
}

.jcc-full-modal__content {
  @include grid-container;
}

.jcc-full-modal__header,
.jcc-full-modal__footer {
  @include grid-row;
}

.jcc-full-modal__body,
.jcc-full-modal__footer {
  @include u-padding-top(6);
}

.jcc-full-modal__body {
  @include grid-row;
  @include grid-gap;
}

.jcc-full-modal__column {
  @include grid-col;
  @include u-width(full);
  @include grid-col(12);

  @include at-media(desktop) {
    @include grid-col(6);
  }
}

.jcc-full-modal__column:only-of-type {
  @include at-media(desktop) {
    @include grid-col(12);
  }
}

.jcc-full-modal__title,
.jcc-full-modal__lead {
  @include u-width(full);
}

.jcc-full-modal--has-two-columns-threequarter {
  @include at-media(desktop) {
    .jcc-full-modal__column:first-of-type {
      @include grid-col(8);
    }

    .jcc-full-modal__column:last-of-type {
      @include grid-col(4);
    }
  }
}

.jcc-full-modal--has-background-color {
  .jcc-full-modal__dialog {
    @include u-bg("primary-lighter");
  }
}
